.App {
  max-width: 1280px;
  text-align: center;
  margin: 0 auto;
}

.globalnavigation {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 15px 0;
}


/*------------------ Kort Formattering ---------------- */

.kortraekke {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.kort {
  position: relative;
  width: 240px;
  margin: 10px;
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3)
}

.kort .tekst {
  text-align: center;
  padding: 0 10px 50px;
}

/*ved "p:nth-of-type(1)" tager jeg fat i det f√∏rste p i "produktkort", som er "{post.beskrivelse}" */
.kort .tekst p:nth-of-type(1){
  min-height: 65px;
}

.kort h3 {
  text-align: center;
  margin-bottom: 5px;
}